import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {GraphClasses} from "@generated/graph-classes";
import {GraphElements} from "@generated/graph-elements";
import {VsanIODiagnosticsFailedCheck} from "@generated/vsan-i-o-diagnostics-failed-check";
import {VsanIODiagnosticsFailedCheckType} from "@generated/vsan-i-o-diagnostics-failed-check-type";
import {VsanIODiagnosticsInstanceResult} from "@generated/vsan-i-o-diagnostics-instance-result";
import {VsanIoLatencyType} from "@generated/vsan-io-latency-type";
import {DomUtil} from "@util/dom-util";
import {VsanUiUtils} from "@util/vsan-util";
import {NodeSingular} from "cytoscape";

export class IoDiagnosticsUtil {

   private static readonly TAG_NAME: string = "div";
   private static readonly DOM_OWNER_LATENCY_TYPES: VsanIoLatencyType[] = [
      VsanIoLatencyType.DOM_OWNER, VsanIoLatencyType.PERF_DOM_OWNER, VsanIoLatencyType.CAPACITY_DOM_OWNER,
   ];

   static createDivElement(): HTMLElement {
      return document.createElement(IoDiagnosticsUtil.TAG_NAME);
   }

   static attachUserEvents(element: HTMLElement, callback: () => any): void {
      DomUtil.onClick(element, callback);
      DomUtil.onEnterKeyUp(element, callback);
   }

   static isDomOwner(type: VsanIoLatencyType): boolean {
      return IoDiagnosticsUtil.DOM_OWNER_LATENCY_TYPES.includes(type);
   }

   static isDomOwnerNode(node: NodeSingular): boolean {
      return node && node.hasClass(GraphClasses.DOM_OWNER_NODE());
   }

   private static UNSUPPORTED_CLUSTER_TYPE: VsanIODiagnosticsFailedCheckType[] = [
      VsanIODiagnosticsFailedCheckType.STRETCHED_CLUSTER,
      VsanIODiagnosticsFailedCheckType.NESTED_FAULT_DOMAIN,
      VsanIODiagnosticsFailedCheckType.COMPUTE_ONLY_CLUSTER,
      VsanIODiagnosticsFailedCheckType.VSAN_OBJECT_IN_REMOTE_DATASTORE,
   ];

   private static getUnsupportedClusterFailedCheck(ioInstanceResult: VsanIODiagnosticsInstanceResult): VsanIODiagnosticsFailedCheck {
      let unsupportedClusterFailedCheck: VsanIODiagnosticsFailedCheck;
      if (ioInstanceResult && ioInstanceResult.precheckResult.failedChecks?.length) {
         unsupportedClusterFailedCheck = ioInstanceResult.precheckResult.failedChecks.find(failedCheck =>
               IoDiagnosticsUtil.UNSUPPORTED_CLUSTER_TYPE.some(clsType => clsType === failedCheck.unsupportedType));
      }
      return unsupportedClusterFailedCheck;
   }

   static createUnavailableViewSpec(ioInstanceResult: VsanIODiagnosticsInstanceResult): UnavailableViewSpec {
      let unavailableViewSpec: UnavailableViewSpec = null;
      const unsupportedClusterFailedCheck: VsanIODiagnosticsFailedCheck =
            IoDiagnosticsUtil.getUnsupportedClusterFailedCheck(ioInstanceResult);
      if (unsupportedClusterFailedCheck) {
         unavailableViewSpec = UnavailableViewSpec.IO_DIAGNOSTICS_NOT_SUPPORTED_CONFIG;
         unavailableViewSpec.statusLabel = unsupportedClusterFailedCheck.reason;
      }
      return unavailableViewSpec;
   }

   static getFailedChecksReasons(failedChecks: VsanIODiagnosticsFailedCheck[]): string[] {
      return failedChecks?.map(failedCheck => failedCheck.reason);
   }

   static getHostName(graphElementDetails: { hostName: string, hostUuid: string }): string {
      return graphElementDetails?.hostName
            ? graphElementDetails.hostName
            : graphElementDetails?.hostUuid;
   }

   static formatLatency(latency: number): string {
      return VsanUiUtils.isUnset(latency)
            ? VsanUiUtils.getString("vsan.na.label")
            : VsanUiUtils.getString("vsan.monitor.vm.iodiagnostics.grid.cell.latency",
                  VsanUiUtils.localizeNumber(latency.toFixed(2)));
   }

   static canRenderGraph(graphElements: GraphElements): boolean {
      return IoDiagnosticsUtil.hasEdges(graphElements) && IoDiagnosticsUtil.hasNodes(graphElements);
   }

   static hasNoStats(graphElements: GraphElements): boolean {
      return !IoDiagnosticsUtil.hasEdges(graphElements) && !IoDiagnosticsUtil.hasNodes(graphElements);
   }

   private static hasEdges(graphElements: GraphElements): boolean {
      return !!graphElements?.edges?.length;
   }

   private static hasNodes(graphElements: GraphElements): boolean {
      return !!graphElements?.nodes?.length;
   }

   static getVmdkName(name: string): string {
      return name.replace("\n", "");
   }
}