import {Component, Input} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {AlertActionLink} from "@component/validation/alert-action-link";
import {VsanIODiagnosticsFailedCheckType} from "@generated/vsan-i-o-diagnostics-failed-check-type";
import {IoDiagnosticsUtil} from "../io-diagnostics.util";
import {VsanIODiagnosticsFailedCheck} from "@generated/vsan-i-o-diagnostics-failed-check";
import {AlertActionLinkUtil} from "@component/validation/alert-action-link-util";

@Component({
   templateUrl: "run-instance-validation.html",
   styleUrls: ["run-instance-validation.scss"],
   selector: "vsan-run-instance-validation",
})
export class RunInstanceValidation {

   @Input()
   failedChecks: VsanIODiagnosticsFailedCheck[];

   @Input()
   clsRef: ManagedObject;

   @Input()
   onPerfServiceEnabled: (taskRef: ManagedObject) => void;

   getFailedChecksReasons(): string[] {
      return IoDiagnosticsUtil.getFailedChecksReasons(this.failedChecks);
   }

   /**
    * For each failed check return null or action (e.g., perf svc action).
    */
   getFailedChecksActionLinks(): AlertActionLink[] {
      return this.failedChecks?.map(failedCheck =>
           (failedCheck.unsupportedType !== VsanIODiagnosticsFailedCheckType.PERF_SVC_DISABLED)
           ? null
           : AlertActionLinkUtil.getPerformanceServiceActionLink({clusterRef: this.clsRef}, this.onPerfServiceEnabled));
   }
}