import {AlertActionLink} from "@component/validation/alert-action-link";
import {VsanUiUtils} from "@util/vsan-util";
import {ModalBuilder} from "@util/modal-builder";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {PerfServiceModalContextData} from "@navigation/context/perf-service-modal-context.data";
import {ManagedObject} from "@service/managed-object";
import {VsanRoutes} from "../../../../vsan-routes";

export class AlertActionLinkUtil {
   static getPerformanceServiceActionLink(context: PerfServiceModalContextData,
         onDialogClose: (taskRef: ManagedObject) => void): AlertActionLink {
      return {
         label: VsanUiUtils.getString("vsan.services.performance.service.enable.action"),
         action: () => {
            new ModalBuilder(VsanRoutes.PERFORMANCE_CONFIG)
                  .setSize(VsanDialogUtil.PERF_SVC_EDIT_ACTION_DIALOG.width,
                           VsanDialogUtil.PERF_SVC_EDIT_ACTION_DIALOG.height)
                  .open(context)
                  .then(onDialogClose);
         },
      };
   }
}